.wpe-like-button__button {
	appearance: none;
	background: none;
	border: none;
	cursor: pointer;
}

.wpe-like-button__button::before {
	background: url("./heart.png") no-repeat;
	background-position: left;
	background-size: 2900%;
	content: "";
	display: block;
	height: 50px;
	width: 50px;
}

.wpe-like-button__button.animate::before {
	animation: heart-burst .8s steps(28) 1;
}

.wpe-like-button__button.wpe-liked:not(.animate)::before {
	background-position: right;
}

.wpe-like-button__button.wpe-liked.animate::before {
	animation-direction: reverse;
	background-position: left;
}

@keyframes heart-burst {
	from {background-position:left;}
	to { background-position:right;}
}
